op: 0px;\r\n  display: block;\r\n  padding: 40px 0;\r\n  text-align: center;\r\n  background: #222;\r\n  transition: all 0.3s ease;\r\n}\r\n\r\n.navbar.show_navbar_menu .navbar__menu{\r\n  left: 0%;\r\n}\r\n\r\n.navbar .navbar__menu .navbar__menu__item {\r\n  margin-top: 45px;\r\n\r\n  min-width: 44px;\r\n  min-height: 44px;\r\n}\r\n\r\n.navbar .navbar__menu .navbar__menu__item a{\r\n  color: #fff;\r\n  font-size: 23px;\r\n  font-weight: 500;\r\n  text-decoration: none;\r\n  margin-left: -100%;\r\n  transition: 0.6s cubic-bezier(0.68, -0.55, 0.265, 1.55);\r\n\r\n  padding: 8px;\r\n}\r\n\r\n.navbar .navbar__menu .navbar__menu__item a:hover{\r\n  color: #ecd353;\r\n}\r\n\r\n.navbar.show_navbar_menu .navbar__menu .navbar__menu__item a{\r\n  margin-left: 0px;\r\n}\r\n\r\n/* End Navbar */\r\n\r\n/* Hero */\r\n.hero {\r\n  object-fit: cover;\r\n  left: 0;\r\n  top: 0;\r\n  height: 100vh;\r\n  width: 100%;\r\n}\r\n/* End Hero */\r\n\r\n.container h1 {\r\n  margin: 20px 0 12px 0;\r\n  font-size: 24px;\r\n}\r\n\r\n/* Card */\r\n.card {\r\n  width: 100%;\r\n  box-shadow: 0 1px 12px #1b1b1b2f;\r\n  border-radius: 8px;\r\n  overflow: hidden;\r\n}\r\n\r\n.card .card__poster {\r\n  width: 100%;\r\n  position: relative;\r\n  display: block;\r\n  transition: all .3s ease;\r\n}\r\n\r\n.card .card__poster:hover {\r\n  transform: scale(1.03);\r\n}\r\n\r\n.card .card__rating {\r\n  position: absolute;\r\n  color: #f1f1f1;\r\n  font-weight: bold;\r\n  margin-top: -50px;\r\n  padding: 4px 8px;\r\n  border-radius: 0 8px 8px 0;\r\n  background-color: #1b1b1b;\r\n  z-index: 1;\r\n}\r\n\r\n.card .card__rating .fa-star {\r\n  color: #ecd353;\r\n}\r\n\r\n.card .card__content {\r\n  margin: 8px 12px;\r\n}\r\n\r\n.card .card__content .card__content__title {\r\n  text-align: center;\r\n  margin: 12px 0 0 0;\r\n}\r\n\r\n.card .card__content .card__content__title a {\r\n  padding: 8px 0;\r\n}\r\n\r\n.card .card__content .card__content__title a {\r\n  text-decoration: none;\r\n  color: #1b1b1b;\r\n}