m__input {\r\n  min-height: 44px;\r\n  padding: 8px;\r\n  border: .1px solid #888;\r\n  border-radius: 8px;\r\n}\r\n\r\n.form__submit_button {\r\n  min-height: 44px;\r\n  min-height: 44px;\r\n  padding: 0 24px;\r\n  border: none;\r\n  background-color: #1b1b1b;\r\n  color: #fff;\r\n  font-weight: 700;\r\n  border-radius: 8px;\r\n  cursor: pointer;\r\n}\r\n\r\n.form__submit_button:hover {\r\n  background-color: #2c2c2c;\r\n}\r\n/* End Add Review */\r\n\r\n/* Favorite Button */\r\n.favorite__button {\r\n  font-size: 18px;\r\n  position: fixed;\r\n  bottom: 20px;\r\n  right: 20px;\r\n  background-color: #1b1b1b;\r\n  color: #fff;\r\n  border: 0;\r\n  border-radius: 50%;\r\n  width: 56px;\r\n  height: 56px;\r\n  cursor: pointer;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  box-shadow: 0 1px 2px #1b1b1b99;\r\n}\r\n/* End Favorite Button */\r\n\r\n/* Footer */\r\nfooter {\r\n  background-color: #1b1b1b;\r\n  padding: 20px 0;\r\n  margin-top: 40px;\r\n}\r\n\r\nfooter .footer__label {\r\n  color: #f1f1f1;\r\n  text-align: center;\r\n  font-size: 14px;\r\n}\r\n/* End Footer */"],"sourceRoot":""}]);
